import { useState, useEffect, useCallback } from 'react';
import { KLineChart } from './components/KLineChart';
import { GamePanel } from './components/GamePanel';
import { GameControls } from './components/GameControls';
import { AccountInfo } from './components/AccountInfo';
import { TradeButtons } from './components/TradeButtons';
import { OrderPanel } from './components/OrderPanel';
import { GameResult } from './components/GameResult';
import { useGameStore } from './stores/gameStore';
import { prepareGameData } from './utils/dataLoader';
import { ETFInfo } from './types/game';

function App() {
  // const [etfData, setEtfData] = useState<ETFInfo[]>([]);
  const [loading, setLoading] = useState(true);
  const [message, setMessage] = useState<{ text: string; type: 'success' | 'error' | 'info' } | null>(null);
  
  const {
    isPlaying,
    isPaused,
    gameSpeed,
    nextKLine,
    resetGame,
    gameResult,
    // notifications,
    // removeNotification
  } = useGameStore();

  // 
  useEffect(() => {
    setLoading(false);
  }, []);

  // 
  useEffect(() => {
    console.log('useEffect:', { isPlaying, isPaused, gameSpeed });
    
    if (!isPlaying || isPaused) {
      console.log(':', { isPlaying, isPaused });
      return;
    }

    console.log(':', 1000 / gameSpeed);
    const interval = setInterval(() => {
      console.log('nextKLine');
      nextKLine();
    }, 1000 / gameSpeed); // 

    return () => {
      console.log('');
      clearInterval(interval);
    };
  }, [isPlaying, isPaused, gameSpeed, nextKLine]);

  // 
  const showMessage = useCallback((text: string, type: 'success' | 'error' | 'info') => {
    setMessage({ text, type });
    setTimeout(() => setMessage(null), 3000);
  }, []);

  // 
  const handleStartGame = useCallback(async () => {
    try {
      showMessage('...', 'info');
      
      const gameData = await prepareGameData();
      if (!gameData) {
        showMessage('', 'error');
        return;
      }

      const { startGame } = useGameStore.getState();
      
      // ETFInfo
      const etfInfo: ETFInfo = {
        code: gameData.stock.code,
        name: gameData.stock.name,
        market: gameData.stock.market,
        data: gameData.data
      };
      
      startGame(etfInfo, gameData.startIndex);
      showMessage(`${gameData.stock.name} (${gameData.stock.code})`, 'success');
    } catch (error) {
      console.error(':', error);
      showMessage('', 'error');
    }
  }, [showMessage]);

  // 
  const handleResetGame = useCallback(() => {
    resetGame();
    showMessage('', 'info');
  }, [resetGame, showMessage]);

  // 
  useEffect(() => {
    const handleKeyPress = (e: KeyboardEvent) => {
      if (!isPlaying) return;

      switch (e.key) {
        case ' ':
          e.preventDefault();
          const { pauseGame, resumeGame } = useGameStore.getState();
          if (isPaused) {
            resumeGame();
          } else {
            pauseGame();
          }
          break;
        case 'ArrowRight':
          e.preventDefault();
          if (isPaused) {
            nextKLine();
          }
          break;
        case '1':
        case '2':
        case '3':
          const speed = parseInt(e.key);
          useGameStore.getState().setGameSpeed(speed);
          break;
      }
    };

    window.addEventListener('keydown', handleKeyPress);
    return () => window.removeEventListener('keydown', handleKeyPress);
  }, [isPlaying, isPaused, nextKLine]);

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-900 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <div className="text-white text-lg">...</div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-900">
      {/*  */}
      {message && (
        <div className={`fixed top-4 left-1/2 transform -translate-x-1/2 z-50 px-6 py-3 rounded-lg shadow-lg transition-all duration-300 ${
          message.type === 'success' ? 'bg-green-600' :
          message.type === 'error' ? 'bg-red-600' : 'bg-blue-600'
        } text-white`}>
          {message.text}
        </div>
      )}

      {/*  */}
      {gameResult && <GameResult />}

      {/*  */}
      <div className="container mx-auto px-4 py-6 max-w-7xl">
        {/*  */}
        <header className="text-center mb-6">
          <h1 className="text-3xl font-bold text-white mb-2">K</h1>
          <p className="text-gray-400">A</p>
        </header>

        {/*  */}
        <div className="mb-6">
          <GamePanel
            onStartGame={handleStartGame}
            onResetGame={handleResetGame}
          />
        </div>

        {/*  */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* K */}
          <div className="lg:col-span-2">
            <KLineChart height={500} />
            
            {/*  */}
            <div className="mt-6">
              <GameControls
                onStartGame={handleStartGame}
                onResetGame={handleResetGame}
              />
            </div>
          </div>

          {/*  */}
          <div className="space-y-6">
            {/*  */}
            <div>
              <TradeButtons onShowMessage={showMessage} />
            </div>

            {/*  */}
            <AccountInfo />

            {/*  */}
            <OrderPanel />


          </div>
        </div>

        {/*  */}
        <footer className="mt-8 text-center text-gray-500 text-sm">
          <p>A | </p>
        </footer>
      </div>
    </div>
  );
}

export default App;
