import React, { useState } from 'react';
import { useGameStore } from '../stores/gameStore';

interface GameControlsProps {
  onStartGame: () => void;
  onResetGame: () => void;
}

export const GameControls: React.FC<GameControlsProps> = ({ onStartGame, onResetGame }) => {
  const [showResetConfirm, setShowResetConfirm] = useState(false);
  const [wasPlayingBeforeConfirm, setWasPlayingBeforeConfirm] = useState(false);
  
  const {
    isPlaying,
    isPaused,
    gameSpeed,
    pauseGame,
    resumeGame,
    setGameSpeed,
    nextKLine,
    isGameOver
  } = useGameStore();

  const speedOptions = [1, 2, 4];

  const handleSpeedChange = () => {
    const currentIndex = speedOptions.indexOf(gameSpeed);
    const nextIndex = (currentIndex + 1) % speedOptions.length;
    setGameSpeed(speedOptions[nextIndex]);
  };

  const handleResetClick = () => {
    // 
    setWasPlayingBeforeConfirm(isPlaying && !isPaused);
    
    // 
    if (isPlaying && !isPaused) {
      pauseGame();
    }
    setShowResetConfirm(true);
  };

  const handleConfirmReset = () => {
    setShowResetConfirm(false);
    setWasPlayingBeforeConfirm(false);
    onResetGame();
    // 
    setTimeout(() => {
      onStartGame();
    }, 100);
  };

  const handleCancelReset = () => {
    setShowResetConfirm(false);
    // 
    if (wasPlayingBeforeConfirm) {
      resumeGame();
    }
    setWasPlayingBeforeConfirm(false);
  };

  return (
    <div className="card">
      {/*  */}
      <div className="flex flex-wrap items-center justify-center gap-3">
        {isPlaying && !isGameOver() && (
          <>
            <button
              onClick={isPaused ? resumeGame : pauseGame}
              className="btn-secondary py-2 px-4"
              title=": /"
            >
              {isPaused ? '' : ''}
            </button>
            
            {isPaused && (
              <button
                onClick={nextKLine}
                className="btn-primary py-2 px-4"
                title=": K"
              >
                
              </button>
            )}
            
            <button
              onClick={handleSpeedChange}
              className="btn-secondary py-2 px-4"
              title="1/2/3: "
            >
              {gameSpeed}x
            </button>
          </>
        )}
        
        {!isPlaying && (
          <button
            onClick={onStartGame}
            className="btn-primary"
          >
            
          </button>
        )}
        
        {isPlaying && (
          <button
            onClick={handleResetClick}
            className="btn-secondary py-2 px-4"
          >
            
          </button>
        )}
      </div>
      
      {/*  */}
      {showResetConfirm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-gray-800 rounded-lg p-6 max-w-sm mx-4 border border-gray-700">
            <h3 className="text-lg font-bold text-white mb-3"></h3>
            <p className="text-gray-300 mb-6">
              
            </p>
            <div className="flex space-x-3">
              <button
                onClick={handleCancelReset}
                className="flex-1 px-4 py-2 bg-gray-600 text-white rounded-lg hover:bg-gray-700 transition-colors"
              >
                
              </button>
              <button
                onClick={handleConfirmReset}
                className="flex-1 px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors"
              >
                
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};
