import React from 'react';
import { useGameStore } from '../stores/gameStore';

export const GameResult: React.FC = () => {
  const { gameResult, resetGame } = useGameStore();

  if (!gameResult) return null;

  const getScoreLevel = (score: number) => {
    if (score >= 90) return { text: '', color: 'text-yellow-400' };
    if (score >= 70) return { text: '', color: 'text-purple-400' };
    if (score >= 50) return { text: '', color: 'text-blue-400' };
    if (score >= 30) return { text: '', color: 'text-green-400' };
    return { text: '', color: 'text-gray-400' };
  };

  const getReturnLevel = (returnRate: number) => {
    if (returnRate >= 20) return { text: '', color: 'text-yellow-400' };
    if (returnRate >= 10) return { text: '', color: 'text-green-400' };
    if (returnRate >= 0) return { text: '', color: 'text-blue-400' };
    if (returnRate >= -10) return { text: '', color: 'text-orange-400' };
    return { text: '', color: 'text-red-400' };
  };

  const scoreLevel = getScoreLevel(gameResult.score);
  const returnLevel = getReturnLevel(gameResult.returnRate);

  return (
    <div className="fixed inset-0 bg-black bg-opacity-75 flex items-center justify-center z-50 p-4">
      <div className="bg-gray-800 rounded-lg p-6 w-full max-w-md max-h-[90vh] overflow-y-auto">
        <h2 className="text-2xl font-bold text-white mb-6 text-center"></h2>
        
        {/*  */}
        <div className="text-center mb-6">
          <div className="text-4xl font-bold text-yellow-400 mb-2">
            {gameResult.score}
          </div>
          <div className={`text-lg font-medium ${scoreLevel.color}`}>
            {scoreLevel.text}
          </div>
        </div>

        {/*  */}
        <div className="space-y-4 mb-6">
          <div className="flex justify-between items-center p-3 bg-gray-700 rounded-lg">
            <span className="text-gray-400"></span>
            <span className="text-xl font-bold text-white">
              {gameResult.finalBalance.toFixed(2)}
            </span>
          </div>

          <div className="flex justify-between items-center p-3 bg-gray-700 rounded-lg">
            <span className="text-gray-400"></span>
            <div className="text-right">
              <div className={`font-semibold ${gameResult.totalReturn >= 0 ? 'price-up' : 'price-down'}`}>
                {gameResult.totalReturn >= 0 ? '+' : ''}{gameResult.totalReturn.toFixed(2)}
              </div>
              <div className={`text-sm ${gameResult.returnRate >= 0 ? 'price-up' : 'price-down'}`}>
                ({gameResult.returnRate >= 0 ? '+' : ''}{gameResult.returnRate.toFixed(2)}%)
              </div>
            </div>
          </div>

          <div className="flex justify-between items-center p-3 bg-gray-700 rounded-lg">
            <span className="text-gray-400"></span>
            <span className={`font-medium ${returnLevel.color}`}>
              {returnLevel.text}
            </span>
          </div>
        </div>

        {/*  */}
        <div className="space-y-3 mb-6">
          <h3 className="text-lg font-semibold text-white mb-3"></h3>
          
          <div className="flex justify-between items-center">
            <span className="text-gray-400 text-sm"></span>
            <span className="text-white">{gameResult.tradeCount} </span>
          </div>

          <div className="flex justify-between items-center">
            <span className="text-gray-400 text-sm"></span>
            <span className="text-white">{gameResult.winRate.toFixed(1)}%</span>
          </div>

          <div className="flex justify-between items-center">
            <span className="text-gray-400 text-sm"></span>
            <span className="text-red-400">{gameResult.maxDrawdown.toFixed(2)}%</span>
          </div>

          <div className="flex justify-between items-center">
            <span className="text-gray-400 text-sm"></span>
            <span className="text-white">{gameResult.duration} K</span>
          </div>
        </div>

        {/*  */}
        <div className="bg-gray-700 rounded-lg p-4 mb-6">
          <h4 className="text-sm font-semibold text-white mb-2"></h4>
          <div className="text-xs text-gray-300 space-y-1">
            <div>   10</div>
            <div>   10</div>
            <div>   2</div>
            <div> 10090</div>
          </div>
        </div>

        {/*  */}
        <div className="flex space-x-3">
          <button
            onClick={resetGame}
            className="flex-1 btn-primary"
          >
            
          </button>
          <button
            onClick={() => window.location.reload()}
            className="flex-1 btn-secondary"
          >
            
          </button>
        </div>

        {/*  */}
        <div className="mt-4 text-center text-xs text-gray-400">
          
        </div>
      </div>
    </div>
  );
};
